#enLink, #deLink {
  opacity: 0.7;
}

#sidebar #sidebarYourSites .login {
  text-align: center;
  margin-top: 15px;
}
#sidebar #sidebarYourSites .consultationList > li {
  padding: 5px 0;
  -webkit-hyphens: auto;
  word-break: break-word;
  -moz-hyphens: auto;
  hyphens: auto;
  overflow: hidden;
}
#sidebar #sidebarYourSites .consultationList > li span.orga {
  font-size: 0.9em;
  color: #767676;
  display: block;
}

.managerAllsitesList ul {
  list-style: none;
  display: block;
  margin: 0;
  padding: 0;
}
.managerAllsitesList ul > li {
  padding: 5px 0;
  display: block;
  -webkit-hyphens: auto;
  word-break: break-word;
  -moz-hyphens: auto;
  hyphens: auto;
  overflow: hidden;
}
@media screen and (min-width: 1024px) {
  .managerAllsitesList ul > li {
    float: left;
    width: 50%;
    height: 60px;
  }
}
.managerAllsitesList ul > li span.orga {
  font-size: 0.9em;
  color: #767676;
  display: block;
}

.manager-navbar-orgaLink a {
  margin-left: 0 !important;
}
.manager-navbar-orgaLink .orga {
  font-size: 0.9em;
  color: #767676;
  display: block;
}

.managerHelpPage {
  font-size: 16px;
}
.well .managerHelpPage.content {
  padding: 15px 40px 30px;
}
.managerHelpPage h2 {
  margin-top: 40px;
}
.managerHelpPage h3 {
  margin-top: 25px;
}
.managerHelpPage ul, .managerHelpPage ol {
  margin-top: 25px;
  margin-bottom: 25px;
}
.managerHelpPage li {
  margin-top: 10px;
  margin-bottom: 10px;
}
.managerHelpPage .toc {
  list-style-type: none;
  margin: 20px 0 60px;
  padding: 0;
}
.managerHelpPage .toc ul {
  margin-top: 0;
}
.managerHelpPage .toc > li > a {
  font-weight: bold;
}
.managerHelpPage .tocFlat {
  margin: 30px 20px 30px;
  padding: 0;
}
.managerHelpPage .helpFigure {
  margin-bottom: 10px;
  margin-top: 10px;
}
.managerHelpPage .helpFigure img {
  max-width: 100%;
}
.managerHelpPage .helpFigure figcaption {
  margin-top: -20px;
  margin-bottom: 20px;
  font-size: 0.8em;
  font-style: italic;
}
.managerHelpPage .helpFigure.center {
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}
@media screen and (min-width: 800px) {
  .managerHelpPage .helpFigure.right {
    width: 50%;
    float: right;
    margin-left: 20px;
  }
}
.managerHelpPage .helpFigure.bordered {
  border: solid 1px grey;
}

.infoSite ul {
  margin-bottom: 25px;
}
.infoSite ul li {
  margin-top: 7px;
}
.infoSite h3 {
  font-size: 1.2em;
  line-height: 1.5;
  margin: 0;
}

.statusReferenceTable {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 20px;
}
.statusReferenceTable col.name {
  width: 220px;
}
.statusReferenceTable tr td, .statusReferenceTable tr th {
  padding-right: 10px;
  padding-bottom: 6px;
  padding-top: 6px;
  border-bottom: solid 1px #ddd;
}